/* custom.css */

.animate-nav {
    opacity: 0;
    transform: translateY(-20px);
}

.logo-link {
    font-size: 1.5rem;
    color: #718096;
    transition: color 0.3s;
    width: 2rem;
    height: 2rem;
    transition: filter 0.3s;
    margin-right: 1rem;
}

.logo-link:hover {
    filter: brightness(1.5);
}

.logo-container {
    display: flex;
    gap: 1rem;
    justify-content: center;
}


.quote {
    font-style: italic;
    color: #718096;
}

.quote-box {
    background-color: #f7fafc;
    border-left: 4px solid #cbd5e0;
    padding: 12px;
    margin: 20px 0;
}

.section-spacing {
    margin-bottom: 2rem;
}

.name {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 1rem;
    color: #4a90e2;
    /* Blue color for the name */
}


/* Apply the underline animation to links with the .underline-hover class */
.underline-hover {
    position: relative;
    display: inline-block;
    text-decoration: none;
    font-style: italic;
    color: #4f595a;
    /* Set link color to a light grey */
}

.underline-hover::before {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    background-color: #ccc;
    /* Set underline color to the same light grey */
    bottom: 0;
    left: 50%;
    /* Position the line at the middle of the link text */
    transform: translateX(-50%);
    transition: width 0.3s ease-in-out;
}

.underline-hover:hover::before {
    width: 100%;
    /* Expand the line to both sides */
}

/* Custom styles for the markdown content */
.markdown-content {
    font-family: 'Arial', sans-serif;
    font-size: 16px;
    line-height: 1.6;
    color: #333;
}

/* Styles for headings */
.markdown-content h1 {
    font-size: 28px;
    /* Increased font size for h1 */
    font-weight: bold;
    margin-bottom: 1rem;
    color: #1e3755;
    /* Dark blue color for h1 */
}

.markdown-content h2 {
    font-size: 24px;
    /* Increased font size for h2 */
    font-weight: bold;
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: #4a5568;
    /* Grey color for h2 */
}

.markdown-content h3 {
    font-size: 20px;
    /* Increased font size for h3 */
    font-weight: bold;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
    color: #6b7280;
    /* Lighter grey color for h3 */
}

.markdown-content h4 {
    font-size: 18px;
    /* Increased font size for h4 */
    font-weight: bold;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
    color: #9ca3af;
    /* Even lighter grey color for h4 */
}

.markdown-content h5 {
    font-size: 16px;
    /* Increased font size for h5 */
    font-weight: bold;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
    color: #bcc1c9;
    /* Pale grey color for h5 */
}

.markdown-content h6 {
    font-size: 14px;
    /* Increased font size for h6 */
    font-weight: bold;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
    color: #d5d9dc;
    /* Lightest grey color for h6 */
}

/* Indentation for lists */
.markdown-content ul,
.markdown-content ol {
    margin-bottom: 1rem;
    margin-left: 2rem;
    /* Indent lists by 2rem */
}

.markdown-content li {
    margin-bottom: 0.5rem;
}

/* Styles for unordered lists */
.markdown-content ul {
    list-style: none;
    /* Remove default bullet styles */
}

.markdown-content ul>li {
    position: relative;
    padding-left: 1rem;
    /* Add padding for bullets */
}

.markdown-content ul>li::before {
    content: "\2022";
    /* Bullet character (default circle) */
    position: absolute;
    left: 0;
}

.markdown-content ul ul>li::before {
    content: "\2013";
    /* En dash character (for second level) */
}

.markdown-content ul ul ul>li::before {
    content: "\25E6";
    /* White bullet character (for third level) */
}

/* Styles for ordered lists */
.markdown-content ol {
    list-style: none;
    /* Remove default number styles */
    counter-reset: list-counter;
    /* Reset counter for ordered lists */
}

.markdown-content ol>li {
    position: relative;
    padding-left: 1rem;
    /* Add padding for numbers */
}

.markdown-content ol>li::before {
    counter-increment: list-counter;
    /* Increment counter for each list item */
    content: counter(list-counter) ". ";
    /* Number followed by a period */
    position: absolute;
    left: 0;
}

.markdown-content ol ol>li::before {
    content: counters(list-counter, ".") " - ";
    /* Number followed by a dash (for second level) */
}

.markdown-content ol ol ol>li::before {
    content: counters(list-counter, ".") " \2022 ";
    /* Number followed by a bullet (for third level) */
}

/* Styles for links */

.markdown-content a {
    position: relative;
    display: inline-block;
    text-decoration: none;
    font-style: italic;
    color: #80949a;
    /* Set link color to a light grey */
}

.markdown-content a::before {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    background-color: #ccc;
    /* Set underline color to the same light grey */
    bottom: 0;
    left: 50%;
    /* Position the line at the middle of the link text */
    transform: translateX(-50%);
    transition: width 0.3s ease-in-out;
}

.markdown-content a:hover::before {
    width: 100%;
    /* Expand the line to both sides */
}


.markdown-content pre {
    display: block;
    padding: 0.5rem;
    background-color: #f7fafc;
    border-radius: 3px;
    font-family: 'Courier New', monospace;
    white-space: pre-wrap;
    /* Preserve new lines and spaces within the code block */
    word-break: break-all;
    /* Allow long words to break within the code block */
    overflow-x: auto;
    /* Add horizontal scroll if necessary */
    max-width: 100%;
    /* Limit the maximum width of the code block */
}

.profile-image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
}

.container {
    max-width: 200px;
    /* Slightly bigger container */
    margin: 0 auto;
}

